/* Big------------------------------ Card*/


.BigCard{
    background-color: white;
    /* width: 37%; */
    height: 400px;
    border-radius: 5px;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    /* justify-content: space-between; */
    position: relative;
    padding: 0;
    margin: 0;  
    margin-bottom: 20px;
}

.Card-div-img{
    background-color: rgb(47, 196, 241);
    border-radius: 5px; 
    height: 250px;
    padding: 0;
    position: relative;
    
   
}

.BigCard-img{
    width: 100%;
    border-radius: 5px;
    height: 250px; 
}


.BigCard-p{
    color: rgb(255, 255, 255);
    font-family: sans-serif;
    line-height: 1.2;
    font-size: 1rem; 
    text-align: center;
    font-weight: 600;
    position: absolute;
    top:40%;
    margin: auto 6%;
    opacity: 1;
    display: none; 
    
}

.BigCard:hover .BigCard-img{
    opacity:0.2;
    transition: all 0.3s ease-out;
    background-color: rgba(1,178,238,.9);   
}

.BigCard:hover .BigCard-p {
  display: block;
  transition: all 0.2s ease-out;
  
  
}

